@import url('https://fonts.googleapis.com/css?family=Lato');

/* TYPE CLASSES ------------------------------------------------------------------------*/

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', helvetica, sans-serif;
}

h1 {
  font-weight: normal;
  color: #EA601D;
  font-size: 55pt;
}

h2 {
  font-weight: normal;
  color: #EA601D;
  font-size: 34pt;
}

h3 {
  font-weight: normal;
  color: #EA601D;
  font-size: 21pt;
}

h4 {
  font-weight: normal;
  color: #0067A9;
  font-size: 13pt;
}

h5 {
  font-weight: normal;
  color: #0067A9;
  font-size: 8pt;
}

/* HELPER CLASSES ------------------------------------------------------------------------*/

.black { color: black }
.orange { color: #EA601D }
.blue { color: #0067A9 }

.clickable { cursor: pointer; -webkit-transition: .1s opacity; -o-transition: .1s opacity; transition: .1s opacity }
.clickable:hover { opacity: .9 }

a { color: inherit; text-decoration: none; }
a:visited { color: inherit; }

/* COSTOM CLASSES ------------------------------------------------------------------------*/

.slice {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: .5;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.slice-selected {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
  opacity: 1;
}
.person-info {
  position: fixed;
  top: 2rem;
  height: 25vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  margin-left: 4rem;
  margin-bottom: 1rem;
}

.header {
  position: fixed;
  top: 2rem;
  right: 3rem;
  height: 1.8rem;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.header-line {
  margin: 0;
  paddingLeft: 1rem;
  borderLeft: 1px solid black;
}

.person-work {
  width: 100%;
  height: 30.9vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

@media screen and (max-width: 800px) {
  .person-info { margin-left: 1rem; margin-right: 1rem; margin-top: -1.5rem; }
  .header { top: 1rem; right: 1rem }
}
@media screen and (max-width: 600px) {
  .person-info { margin-left: 2rem; margin-right: 2rem; margin-top: 2rem; }
  .header {
    -ms-flex-pack: justify;
        justify-content: space-between;
    right: 0;
    top: 1.5rem;
    width: 100%;
    padding: 0 2rem;
  }
  .header-line { border: none; padding: 0 }
}



